import { Redirect } from "react-router";
import { useEffect, useState } from "react";
import axios from "axios";
let UserScore = ({ user, testScore }) => {
  
  let [submitted, setSubmitted] = useState(false);
  useEffect(() => {
    let totalScore = 0;
    let formattedScoreForApi = [];
    for (let x in testScore) {
      totalScore += testScore[x];
    }
    for (let i = 1; i <= 5; i++) {
      formattedScoreForApi[i - 1] = testScore[`level${i}`];
    }
    axios
      .post("https://livioscreeningapi.herokuapp.com/screening/submit", {
        email: user.email,
        individualScore: formattedScoreForApi,
        totalScore,
      })
      .then(() => {
        setSubmitted(true);
      })
      .catch((error) => {
        alert("Error!");
        console.log(error);
      });
  }, []);
  return (
    <>
      {user ? (
        <div className="container p-4">
          <h5>{user.name}</h5>
          <h5>{user.email}</h5>
          <hr />
          {submitted ? (
            <h2>Thank you for applying to Livio. Your Test has been submitted</h2>
          ) : (
            <h2>Please wait while we submit your test.</h2>
          )}
        </div>
      ) : (
        <Redirect to="/" />
      )}
    </>
  );
};

export default UserScore;
